.loginScreen{
    position: relative;
    height: 100%;
    background: url("https://user-images.githubusercontent.com/33485020/108069438-5ee79d80-7089-11eb-8264-08fdda7e0d11.jpg") center no-repeat;
    background-size: cover;
}

.loginScreen_Gradient{
    width: 100%;
    z-index: 1;
    height: 100vh;
    background: rgba(0, 0, 0, 0.4);
    background-image: linear-gradient(to top,
    rgba(0,0,0,0.8)0,
    rgba(0,0,0,0)60%,
    rgba(0,0,0,0.8)100%
    );
}

.loginScreeen_logo{
    position: fixed;
    left: 0;
    width: 150px;
    object-fit: contain;
    padding-left: 20px;
    padding-top: 10px;
}

.loginScreen_Button{
    position: fixed;
    width: auto;
    height: 4%;
    size: auto;
    right: 20px;
    top: 20px;
    padding: 10px, 20px;
    font-size: 1rem;
    color: white;
    background-color: #e50914;
    font-size: 600;
    font-weight: 300;
    border: none;
    cursor: pointer;
    border-radius: 3px;
    
}

.loginScreen_body{
    position: absolute;
    top: 30%;
    margin-left: auto;
    margin-right: auto;
    text-align: center;
    z-index: 1;
    color: #fff;
    padding: 20px;
    left: 0;
    right: 0;
}

.loginScreen_body > h1{
    font-size: 3.125rem;
    margin-bottom: 20px;
}

.loginScreen_body > h2{
    font-size: 2rem;
    font-weight: 400;
    margin-bottom: 30px;
}

.loginScreen_body > h3{
    font-size: 1.3rem;
    font-weight: 400;
}

.loginScreen_input > form > input{
    padding: 10px;
    outline-width: 0;
    height: 30px;
    width: 30%;
    border: none;
    max-width: 600px;
}

.loginScreen_input{
    margin: 20px;
}

.loginScreen_getStarted{
    padding: 16px 20px;
    font-size: 1rem;
    color: white;
    background-color: #e50914;
    border: none;
    font-weight: 600;
    cursor: pointer;
}